.menu-wrap {
  z-index: 10;
  justify-content: flex-end;
  display: none;
  position: relative;
}

.menu-wrap .hamburger {
  z-index: 1;
  width: 50px;
  height: 50px;
  flex-direction: column;
  align-items: flex-end;
  padding: 1rem;
  display: flex;
  position: relative;
}

.menu-wrap .hamburger .line--1 {
  width: 40px;
  height: 3px;
  background: #231f20;
  flex: none;
  justify-content: center;
  align-items: center;
  transition: all .4s;
  display: flex;
  position: absolute;
  top: 32px;
}

.menu-wrap .hamburger .line--2 {
  z-index: 1;
  width: 28px;
  height: 3px;
  background: #231f20;
  transition: all .4s;
  position: absolute;
  top: 42px;
}

.menu {
  z-index: 15;
  -webkit-backdrop-filter: blur(3px);
  backdrop-filter: blur(3px);
  width: 80%;
  height: 100vh;
  background-color: #ffffffd0;
  transition: all 1s;
  position: fixed;
  top: 10vh;
  right: 0;
  overflow-y: scroll;
  transform: translateX(150%);
}

.menu div {
  text-align: left;
  justify-content: center;
  margin: auto;
  display: flex;
}

.menu div ul {
  padding: 0 0 200px;
}

.menu div ul li {
  color: #231f20;
  border-bottom: 1px solid #444;
  padding: .8rem 0;
  font-size: 1.7rem;
  font-weight: 600;
  list-style: none;
  position: relative;
}

.menu div ul li a {
  color: inherit;
  text-decoration: none;
  transition: color .4s;
  position: relative;
}

.menu div ul li a .arrow--down, .menu div ul li a .arrow--up {
  position: absolute;
  top: 5%;
  left: 120%;
}

.menu div ul li:last-child {
  border-bottom: none;
}

.menu div ul li .menu--prodotti__ul {
  padding: 20px 0;
}

.menu div ul li .menu--prodotti__ul li {
  text-transform: uppercase;
  border: none;
  font-size: .8rem;
}

