.navbar {
  z-index: 10;
  height: 10vh;
  background: #fff;
  grid-template-columns: 1fr 2fr;
  align-items: center;
  padding: 0 4.1vw;
  transition: all .3s ease-in-out;
  display: grid;
  position: sticky;
  top: 0;
  left: 0;
}

.navbar .navbar__logo {
  width: 170px;
}

.navbar .navbar__logo img {
  width: 100%;
}

.navbar .navbar__ul {
  text-transform: uppercase;
  justify-content: end;
  margin: 0;
  padding: 0;
  font-weight: 700;
  display: flex;
}

.navbar .navbar__ul li {
  padding: 0 2vw;
  line-height: 2rem;
}

.navbar .navbar__ul li span {
  color: #0000;
  font-size: .7rem;
}

.navbar .navbar__ul li a {
  align-items: unset;
  display: inline-block;
}

.navbar .navbar__ul li a:hover {
  color: #be1624;
  transform-origin: top;
  transition: all .5s;
  transform: scale(1.1);
}

.navbar .navbar__ul li a:hover:after {
  width: 100%;
  background: #be1624;
}

.navbar .navbar__ul li a:after {
  content: "";
  height: 2px;
  width: 0;
  background: none;
  transition: width .5s, background-color .5s;
  display: block;
}

.navbar .navbar__ul .navbar__prodotti {
  position: relative;
}

.navbar .navbar__ul .navbar__prodotti a:hover {
  transition: all .3s;
}

.navbar .navbar__ul .navbar__prodotti .prodotti__ul {
  top: end;
  width: 180%;
  visibility: hidden;
  opacity: 0;
  background-color: #ffffffd0;
  margin-top: 15px;
  padding: 30% 0 10%;
  transition: all .3s;
  position: absolute;
  left: 0;
}

.navbar .navbar__ul .navbar__prodotti .prodotti__ul li {
  margin: 5px 0;
  font-size: .8rem;
  font-weight: 600;
  line-height: 2rem;
}

